/*===== GOOGLE FONTS =====*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");
/*===== VARIABLES CSS =====*/

/*Mise en place de ma grid*/
*{
  padding: 0px;
  margin: 0px;
}

body{
  color: white;
}

/*---------HEADER*/
.l-header{
  display: flex;
  flex-direction: row;
  background-color: #370617;

}

/*--------PRESENTATION-----------------*/
.Presentationsection{
  background-color: #03071E;
}

.Presentation{
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  font-size: 35px;
}

/*
.Presentation > :first-child{
  background-color: red;
  padding-top: px;
}*/

.Presentation > .item2{
  margin-top: 23vh;
  padding-left: 20px  ;
}

.item2{
  margin: auto;
}

.Presentation > .item2 > .buttpresentation{
  margin-top: 50px;
  margin-right: 20px;
  background-color:#F48C06;
}

 .containernavheader{
  display: flex;
  flex-wrap: wrap;
}

.navheaderitem{
  margin-right: 10px;
  
}

.containercontact{
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  width: 100%;
  min-height: 100vh;
  z-index: 999;
 /* box-shadow: 10px 10px 10px rgb(27, 18, 18);*/
 background-color: rgba(0,0 ,0 , 0.9);
}

.itemcontact{
  position: absolute;
  margin-top: 10%;
  transform: scale(1.25,1.25);
  width: 500px;
  animation-iteration-count:2;
  animation: 1s linear 0s moveeye;
}
.icon{
  text-align: right;
}

.icon:hover{
  color: #F48C06;
}

.itemcontact > .item3{
  background-color:white;
}

form {
  text-align: center;
}

form > .MuiFormControl-root{
  width: 400px;
}

@keyframes moveeye{
  0%{
    /*box-shadow: 2px 2px 2px rgb(27, 18, 18);*/
    transform: scale(0,0);
    transform:rotate(360deg);
    opacity: 0.2;
  }
  50%{
    /*box-shadow: 4px 4px 4px rgb(27, 18, 18);*/
    transform: scale(0,0);
    opacity: 0.4;
    transform:rotate(810deg);
  }
}

.but{
  background-color: red;
}


 .container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  bottom: 0px;
  background-color: blueviolet;
  position: fixed;
  transform: scale(0,0);
  box-shadow: inset 0px 6px 0px rgb(220,220,220)
}

.item1{
  margin: 10px;
  background-color: red;
}




