.containercontact{
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  width: 100%;
  min-height: 100vh;
  z-index: 999;
 /* box-shadow: 10px 10px 10px rgb(27, 18, 18);*/
 background-color: rgba(0,0 ,0 , 0.9);
}

.itemcontact{
  position: absolute;
  margin-top: 10%;
  -webkit-transform: scale(1.25,1.25);
          transform: scale(1.25,1.25);
  width: 500px;
  -webkit-animation-iteration-count:2;
          animation-iteration-count:2;
  -webkit-animation: 1s linear 0s moveeye;
          animation: 1s linear 0s moveeye;
}
.icon{
  text-align: right;
}

.icon:hover{
  color: #F48C06;
}

.itemcontact > .item3{
  background-color:white;
}

form {
  text-align: center;
}

form > .MuiFormControl-root{
  width: 400px;
}

@-webkit-keyframes moveeye{
  0%{
    /*box-shadow: 2px 2px 2px rgb(27, 18, 18);*/
    -webkit-transform: scale(0,0);
            transform: scale(0,0);
    -webkit-transform:rotate(360deg);
            transform:rotate(360deg);
    opacity: 0.2;
  }
  50%{
    /*box-shadow: 4px 4px 4px rgb(27, 18, 18);*/
    -webkit-transform: scale(0,0);
            transform: scale(0,0);
    opacity: 0.4;
    -webkit-transform:rotate(810deg);
            transform:rotate(810deg);
  }
}

@keyframes moveeye{
  0%{
    /*box-shadow: 2px 2px 2px rgb(27, 18, 18);*/
    -webkit-transform: scale(0,0);
            transform: scale(0,0);
    -webkit-transform:rotate(360deg);
            transform:rotate(360deg);
    opacity: 0.2;
  }
  50%{
    /*box-shadow: 4px 4px 4px rgb(27, 18, 18);*/
    -webkit-transform: scale(0,0);
            transform: scale(0,0);
    opacity: 0.4;
    -webkit-transform:rotate(810deg);
            transform:rotate(810deg);
  }
}

